@media (prefers-reduced-motion: no-preference) {
    #pic1 {
        animation: App-logo-spin infinite 15s linear;
    }
}

.App-header {
    background-color: #ff8243;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(20px + 2vmin);
    color: white;
    border-bottom-style: ridge;
}

.line1 {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-gap: 10px;
    grid-auto-rows: 100px;
    grid-template-areas:
        "a a b b b b b c"
        "a a b b b b b c";
    align-items: start;
    width: 200%;
}

#pic1 {
    grid-area: a;
    width: 40%;
}

.SearchBar {
    align-content: center;
    margin-left: 1%;
    margin-top: 2%;
    grid-area: b;
}

#title {
    margin-left: 5%;
    justify-content: center;
    font-size: calc(8px + 2vmin);
    color: #ff8243;
}

.title2 {
    font-size: calc(3px + 2vmin);
    color: white;
    background-color: #ff8243;
}

#SearchCity,
#SearchCountry {
    border: none;
    border-bottom: 2px solid red;
    font-family: serif;
    height: 30px;
    width: 30%;
}

#SearchCountry {
    margin-left: 20px;
}

.getWeatherbutton {
    margin-left: 45px;
    width: 20%;
    background-color: #ff8243;
    padding: 7px 5%;
    text-align: center;
    font-size: 16px;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;
    border-radius: 12px;
    color: white;
}

.Mapbox {
    display : inline-table;
    margin-top: -1%;
    height: 400px;
    width: 200%;
    position: relative;
    background-color: white;

}

.description{
  align-items: center;
}

.nestedDescTable {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-gap: 2%;
    grid-auto-rows: 100px;
    grid-template-areas:
        "d d d e e f f "
        "a b b b b c c "
        "a b b b b c c "
        "a b b b b c c ";
    align-items: start;
}

.column1Titre {
  margin-left: -45%;
    grid-area: d;
    font-weight: bold;
}

.column2Titre {
   margin-left: -100%;
    grid-area: e;
    font-weight: bold;

}

.DescText {
    margin-top: -80%;
    margin-left: 50%;
    grid-area: a;
}

.DescTime {

    margin-top: -19%;
    margin-left: 3%;
    grid-area: b;

}

.DescImag {
    margin-top: -55%;
    grid-area: c;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
    grid-auto-rows: 100px;
    grid-template-areas:
        "a a a a "
        "b b b b "
        "c c c c ";
    align-items: start;
}

#pic3,
#pic4,
#pic5 {
    width: 60%;
}

#pic3 {
    grid-area: a;
}

#pic4 {
    grid-area: b;
}

#pic5 {
    grid-area: c;
}

.plot {
    padding-top: 1%;
}
table, th, tr {
  margin-left: auto; 
  margin-right: auto; 
  border: 1px solid black;
  border-color: #ff8243;
}
th{
  width: 50%;
  align-items: center;
}
tr{
  width: 100%;
}
.plot2 {
    padding-top: 3%;
}

.plot3 {
    padding-top: 3%;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(180deg);
    }
}